@import url(https://fonts.googleapis.com/css?family=Open+Sans:300&display=swap);
body {
  margin: 0;
  font-family:'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  width: 100vw;
  height: 100vh;
}

.logo {
  width: 50vmin;
  position: relative;
  padding: 1%;
}


.buttonForms{
    display: inline-block;
    background-color: #3dade2;
    border: none;
    border-radius: 10px;
    padding: 1vmin 5vmin ;
    color: #fff;
    font-weight: bold;
}

.form{
    display: inline-block;
}
.column{
    display: inline-block;
}

.buttonCharts{
    margin-left: 40%;
    display: block;
    background-color: #3dade2;
    border: none;
    border-radius: 10px;
    padding: 3vmin ;
    color: #fff;
    font-weight: bold;
    font-size: 16px
}

.tableAnnual{
    display: block;
    margin: 2% 20%;
    width: 60%;
   
}
.column{
    width: 200px;
    padding: 10px;
    border-bottom: 1px solid #ddd;
}
.title{
    border-bottom: 1px solid #ddd;
    font-weight: bold;
}


th {
    width: 200px;
    padding: 10px;
    border-bottom: 1px solid #ddd;
  }

td{
    padding: 10px;
}

